.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0.44rem;
  background-color: #ee742f;
  ul {
    display: flex;
    position: relative;
    height: 0.3rem;
    font-size: 0.14rem;
    color: #fff;
    border: 0.01rem solid #fff;
    border-radius: 0.18rem;
    li {
      width: 0.63rem;
      text-align: center;
      height: 100%;
      line-height: 0.28rem;
      color: #fff;
      transition: all 0.4s ease-out;
      z-index: 99;
      &.active {
        color: #ee742f;
      }
      &.slider {
        position: absolute;
        top: 0;
        left: 0;
        background-color: #fff;
        z-index: 10;
        border-radius: 0.18rem;
        &.right {
          left: 0.63rem;
        }
      }
    }
  }
}