.detail-wrap {
  img {
    width: 100%;
  }
  .detail {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 1.7rem;
    h1 {
      font-size: 0.16rem;
    }
    h2 {
      font-size: 0.14rem;
      margin: 0.1rem 0;
    }
    button {
      width: 0.66rem;
      height: 0.33rem;
      background-color: #ee742f;
      border: 0;
      font-size: .14rem;
      border-radius: 0.03rem;
    }
  }
}