.classify {
  display: flex;
  height: 100%;
  .left {
    width: 20%;
    height: 100%;
    overflow-y: scroll;
    li {
      width: 100%;
      height: 0.48rem;
      line-height: 0.5rem;
      text-align: center;
      font-size: 0.16rem;
      background:#f3f3f3;
      span {
        height: 100%;
      }
      &.active {
        background-color: #fff;
        span {
          display: inline-block;
          border-bottom:0.02rem solid #ee742f;
        }
      }
    }
  }
  .right {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    width: 80%;
    height: 100%;
    overflow-y: scroll;
    li {
      width: 33.3%;
      height: 0.5rem;
      line-height: 0.5rem;
      text-align: center;
      font-size: 0.16rem;
      color: #666;
    }
  }
}