@import '../../assets/style/border.scss';
@import '../../assets//style/ellipsis.scss';

.list {
  height: 100%;
  ul {
    height: 100%;
    overflow-y: scroll;
    li {
      display: flex;
      padding: 0.1rem;
      background-color: #fff;
      @include border(0 0 1px 0);
      img {
        width: 1.5rem;
        height: 1rem;
        margin-right: 0.15rem;
      }
      .content {
        line-height: 0.27rem;
        h1, h2 {
          font-size: 0.16rem;
          @include ellipsis(2);
        }
        h2 {
          font-weight: normal;
          font-size: 0.14rem;
        }
        p {
          line-height: 0.2rem;
        }
      }
    }
  }
}